export class LapB {
    constructor(pkg = "default.web.pkg",
                uuid = "default-web-uuid",
                size = LapBSizes["BANNER"],
                bannerClass = "lap_b",
                autoRender = true,
                parentEl) {
        this.size = size;
        this.uuid = uuid;
        this.pkg = pkg;
        this.reloadAuto = autoRender;
        this.parentEl = parentEl;
        this.bannerClass = bannerClass;

        this.#create();
    }

    static setBanners(
        pkg = "default.web.pkg",
        uuid = "default-web-uuid",
        defaultBannerSize = LapBSizes["BANNER"],
        bannerClass = "lap_b",
        autoRender = true
    ) {
        const bannersEls = document.querySelectorAll('[data-lap-b]');
        bannersEls.forEach((bannerEl) => {
            const size = bannerEl.dataset.lapB || defaultBannerSize;
            new LapB(pkg, uuid, size, bannerClass, autoRender, bannerEl);
        })
    }

    #create() {
        this.parentEl.id = 'lap_b';
        this.parentEl.classList.add(this.bannerClass);
        const size = LapBSizes[this.size.toUpperCase()] || LapBSizes['BANNER'];
        this.parentEl.style.width = size.width;
        this.parentEl.style.height = size.height;
        this.parentEl.appendChild(this.#createIframe());
    }

    #createIframe() {
        const iframe = document.createElement('iframe');
        iframe.src = "https://ads-api.koshersvr.com/banner_ad/view?" + this.#getParams();
        iframe.id = "lap_b_iframe";
        iframe.onload = () => console.log("iframe loaded");
        iframe.onerror = () => iframe.style.display = 'none';
        return iframe;
    }

    #getParams() {
        return new URLSearchParams({
            size: this.size || 'BANNER',
            pkg: this.pkg,
            device_id: this.uuid,
            reload_auto: this.reloadAuto,
        }).toString();
    }
}

export const LapBSizes = {
    "BANNER": {
        width: "320px",
        height: "50px"
    },
    "LARGE_BANNER": {
        width: "320px",
        height: "100px"
    },
    "MEDIUM_RECTANGLE": {
        width: "300px",
        height: "250px"
    }
}