export class LapN {
    constructor(
        pkg,
        uuid,
        nativeClass,
        parentEl) {
        this.url = "https://ads-api.koshersvr.com/native_ad"
        this.pkg = pkg;
        this.uuid = uuid;
        this.el = parentEl;
        this.nativeClass = nativeClass;
        if (!this.el) return;

        this.fetch().then(() => this.build());
        this.scrollListener = () => this.impression();
        window.addEventListener('scroll', this.scrollListener, {passive: true});
    }

    static setNatives(pkg = "default.web.pkg", uuid = "default-web-uuid", nativeClass = "lap_n") {
        const nativeEls = document.querySelectorAll('[data-lap-n]');
        nativeEls.forEach((nativeEl) => {
            new LapN(pkg, uuid, nativeClass, nativeEl);
        })
    }


    async fetch() {
        const url = this.url + "?" + this.getParams();
        const results = await fetch(url)
        this.data = await results.json();
    }

    build() {
        this.el.classList.add(this.nativeClass)
        this.el.style.cursor = 'pointer';

        const title = document.createElement('h4');
        title.innerText = this.data.title || 'Title';
        title.classList.add('lap_n_title');

        const content = document.createElement('p');
        content.innerText = this.data.content || null;
        content.classList.add('lap_n_content');

        const text = document.createElement('div');
        text.classList.add('lap_n_text');
        text.appendChild(title);
        text.appendChild(content);

        const image = document.createElement('img');
        image.src = this.data.image_url || '';
        image.classList.add('lap_n_image');

        this.el.appendChild(text)
        this.el.appendChild(image)
        this.el.addEventListener("click", () => window.open(this.data.url));
    }

    getParams() {
        return new URLSearchParams({
            pkg: this.pkg,
            device_id: this.uuid
        }).toString();
    }

    getImpressionParams() {
        return new URLSearchParams({
            pkg: this.pkg,
            device_id: this.uuid,
            id: this.data.id,
            name: this.data.name,
            campaign: this.data.campaign,
        }).toString();
    }

    impression() {
        const isVisible = this.isVisible();
        if (isVisible) {
            window.removeEventListener('scroll', this.scrollListener);
            const url = this.url + "/impression?" + (() => this.getImpressionParams())();
            fetch(url)
        }
    }

    isVisible() {
        const rect = this.el.getBoundingClientRect();
        const viewHeight = Math.max(document.documentElement.clientHeight, window.innerHeight);
        const visible = !!( this.el.offsetWidth || this.el.offsetHeight || this.el.getClientRects().length );
        return visible && !(rect.bottom < 0 || rect.top - viewHeight >= 0);
    }

}