import {Lap} from "./Lap";

document.initLap = (pkg, options =  {}) => {
    console.log("Lap initialized");
    options = options || {};
    options.visitsCount = options.visitsCount || 4;
    options.autoRender = options.autoRender || true;
    options.defaultBannerSize = options.defaultBannerSize || 'BANNER';
    options.bannerClass = options.bannerClass || 'lap-b';
    options.nativeClass = options.nativeClass || 'lap-n';

    document.lap = {
        lap: new Lap(pkg, options),
        version: "1.0.0",
        status: "initialized"
    }
}