import {v4 as uuidv4} from 'uuid';

import {LapIN} from "./LapIN";
import {LapB} from "./LapB";
import {LapN} from "./LapN";

import "./styles.scss";


export class Lap {
    constructor(pkg, options) {
        this.pkg = pkg;
        this.uuid = this.#getUUID();
        this.visitsCount = options.visitsCount;
        this.autoRender = options.autoRender;
        this.defaultBannerSize = options.defaultBannerSize;
        this.bannerClass = options.bannerClass;
        this.nativeClass = options.nativeClass;

        this.initIN();
        LapB.setBanners(this.pkg, this.uuid, this.defaultBannerSize, this.bannerClass, this.autoRender);
        LapN.setNatives(this.pkg, this.uuid, this.nativeClass);
    }

    initIN() {
        const previousVisits = this.#getCookie('lap_c');
        if (!previousVisits) this.#setCookies('lap_c', 0); else this.#setCookies('lap_c', parseInt(previousVisits) + 1);
        if (previousVisits > this.visitsCount - 1) {
            new LapIN(this.pkg, this.uuid);
            this.#setCookies('lap_c', 0);
        }
    }

    #getCookie(name) {
        const value = `; ${document.cookie}`;
        const parts = value.split(`; ${name}=`);
        if (parts.length === 2) return parts.pop().split(';').shift();
    }

    #setCookies = (name, value) => document.cookie = `${name}=${value}`;

    #getUUID() {
        const fromCookie = this.#getCookie('lap_u');
        if (fromCookie) {
            return fromCookie;
        } else {
            this.uuid = uuidv4();
            this.#setCookies('lap_u', this.uuid);
            return this.uuid;
        }
    }
}