export class LapIN {
    div = document.createElement('div');
    closeButton = document.createElement('button');
    iframe = document.createElement('iframe');

    url = null;

    packageName = null;
    uuid = null;

    constructor(
        packageName,
        uuid
    ) {
        this.url = "https://ads-api.koshersvr.com/interstitial_ad/view?" + this.createParams();
        this.packageName = packageName;
        this.uuid = uuid;
        this.show()
    }

    show() {
        this.create();
    }

    onload = () => {
        setTimeout(() => {
            this.closeButton.style.opacity = "1";
        }, 3000)
    }

    hide() {
        document.body.removeChild(document.getElementById("lap_in"));
    }

    create() {
        this.createDiv()
        this.createIframe()
        this.createCloseButton()
    }

    createDiv() {
        this.div.id = "lap_in";
        document.body.appendChild(this.div);
    }

    createIframe() {
        this.iframe.src = this.url;
        this.iframe.id = "lap_in_iframe";
        this.div.appendChild(this.iframe);
        this.iframe.onload = this.onload;
    }

    createCloseButton() {
        this.closeButton.id = "lap_in_close";
        this.closeButton.innerText = "X";
        this.closeButton.addEventListener("click", this.hide);
        this.div.appendChild(this.closeButton);
    }

    createParams() {
        return new URLSearchParams({
            "pkg": this.packageName,
            "device_id": this.uuid
        }).toString()
    }
}